<template>
  <div>
    <div class="swiper-container">
　　  <div class="swiper-wrapper">
　　　　 <div class="swiper-slide" data-swiper-autoplay="5000" v-for="(item, index) in 8" :style="{backgroundImage: 'url(http://mziu.club/banner-0'+(index+1)+'.png)'}" :key="item.key"></div>
　　　</div>
     <!-- <div class="swiper-button-prev"></div>
     <div class="swiper-button-next"></div> -->
     <div class="swiper-pagination"></div>
　　</div>
    <div id="chituo">
      <div class="chituo">
        <span>炽于心</span> <span>●</span> <span>拓于行</span>
        <div class="chituo__block chituo__top"></div>
        <div class="chituo__block chituo__bot"></div>
      </div>
      <div class="chituoen">
        {{ msg }}
        <span class="animated" :class="ind === true? 'fadeIn': 'fadeOut'">{{ icon }} </span>
        <!-- <span>Ｃｈì ｙú ｘīｎ</span>
        <span>.</span>
        <span>Ｔｕò ｙú ｘíｎｇ</span> -->
      </div>
    </div>
    <team-item></team-item>
    <work-show-item></work-show-item>
  </div>
</template>
<script>
import teamItem from '../../components/teamItem'
import workShowItem from '../../components/workShowItem'
import Swiper from "swiper"
export default {
  name: 'home',
  data () {
    return {
      msg: ``,
      icon: '_',
      ind: true,
      hands: ''
    }
  },
  components: { //  引入组件
    teamItem,
    workShowItem
  },
  computed: {
  },
  mounted () {
    var swiper = new Swiper(".swiper-container", {
      autoplay: true, //  自动播放
      speed: 1000, //  切换速度
      effect: 'slide', //  切换效果:渐隐
      grabCursor: true, //  鼠标经过切换为小手
      loop: true, //  循环
      pagination: {
        el: '.swiper-pagination',
        bulletActiveClass: 'my-bullet-active',
      },
　　})
  },
  beforeMount () {
    let info = `Ｃｈì ｙú ｘīｎ . Ｔｕò ｙú ｘíｎｇ`
    setTimeout(() => {
      this.pt(info)
    }, 3000);
    var init = self.setInterval(() => { //  模拟打字机效果
      if (this.msg.length >= info.length-7) {
        clearInterval(init)
      }
      if (this.ind === true) {
        this.ind = false
      } else {
        this.ind = true
      }
    }, 200)
  },
  methods: {
    pt (t) { //  打印方法
      let index = 0
      var int = self.setInterval(() => {
        if (index > t.length) { //  判断当前的索引大于文字的长度
          clearInterval(int)
        } else {
          this.msg = t.substr(0, index)
          index++
        }
      }, 150)
    }
  }
}
</script>
